iv:first-child {\n        background-color: themed(tertiary);\n        color: themed(contrast);\n    }\n\n    .lichess > div:first-child {\n        background-color: themed(secondary);\n        color: themed(contrast);\n    }\n}\n\n#about {\n    text-decoration: underline;\n    text-underline-offset: 8px;\n}\n\n#about-box {\n    display: flex;\n}\n\n#about-content {\n    flex-grow: 1;\n    color: white;\n    min-height: 500px;\n    max-height: 531px;\n    padding: 32px;\n    border-radius: 10px;\n    margin-right: 16px;\n    line-height: 1.5;\n    overflow: auto;\n\n    h2 {\n        margin: 0;\n    }\n}\n\n.gt {\n    color: rgb(179, 163, 105);\n    font-weight: bold;\n}\n\n.slider-container {\n    display: flex;\n    flex-direction: row;\n    width: 100%;\n\n    > div {\n        display: flex;\n        flex-direction: column;\n        color: black;\n        padding: 16px;\n        border-radius: 10px;\n    }\n\n    h2 {\n        text-align: center;\n    }\n}\n\n.content {\n    margin-top: 8px;\n    padding: 0;\n\n    img {\n        display: block;\n        margin-left: auto;\n        margin-right: auto;\n        margin-top: 16px;\n        width: 100%;\n    }\n}\n\n.active {\n    flex: 5;\n}\n\n.inactive {\n    justify-content: center;\n    flex: 1;\n\n    h2 {\n        transform: rotate(90deg);\n    }\n\n    .content {\n        display: none;\n    }\n}\n\n.media {\n    display: grid;\n    grid-template-columns: repeat(3, 60px);\n    row-gap: 8px;\n    margin-top: 32px;\n\n    svg {\n        width: 100%;\n    }\n}\n\n.speech {\n    position: relative;\n    grid-column: 1 / 4;\n    text-align: center;\n    padding: 4px;\n    border-radius: 10px;\n\n    &::after {\n        position: absolute;\n        left: calc(50% - 15px);\n        bottom: -15px; \n        display: block; \n        content: \"\";\n        width: 0; \n        border: 15px solid transparent;\n        border-bottom: 0;\n    }\n}\n\n#resume-btn {\n    grid-column: 1 / 4;\n    font-size: medium;\n    text-align: center;\n    padding: 4px;\n    border-radius: 10px;\n}\n\n.connection {\n    display: flex;\n    flex-wrap: wrap;\n    gap: 8px;\n\n    div:last-child {\n        display: flex;\n        justify-content: end;\n        align-items: end;\n        flex-grow: 1;\n        margin-bottom: -32px;\n    }\n}\n\n#connection-msg {\n    width: min(350px, 100%);\n    margin: 0;\n}\n\n.animation-img-container {\n    display: flex;\n    gap: 8px;\n\n    div {\n        display: flex;\n        flex-direction: column;\n    }\n\n    video, img {\n        margin-top: 0;\n        width: 100%;\n        border-radius: 10px;\n    }\n\n    p {\n        padding: 0 16px 0;\n        text-align: center;\n    }\n}\n\n.chess-stats-container {\n    display: flex;\n    flex-wrap: wrap;\n    gap: 8px;\n    font-size: large;\n\n    > div {\n        display: grid;\n        justify-content: center;\n        align-content: space-between;\n        padding: 16px;\n        grid-template-columns: repeat(3, 33.33%);\n        grid-template-rows: repeat(3, min-content);\n        gap: 8px;\n        border-radius: 10px;\n\n        > div {\n            display: flex;\n            justify-content: center;\n            align-items: center;\n            background-color: lightgray;\n            color: black;\n            padding: 8px;\n            border-radius: 10px;\n            box-shadow: 5px 5px 5px black;\n        }\n    }\n}\n\n.chesscom {\n    flex: 3;\n}\n\n.lichess {\n    flex: 1;\n}\n\n.full-row {\n    grid-column: 1 / -1;\n}\n\n.stat {\n    display: flex;\n    flex-direction: column;\n    text-align: center;\n}\n\n.number {\n    font-size: larger;\n    font-weight: bold;\n}\n  \n#about-options {\n    display: flex;\n    flex-direction: column;\n    gap: 32px;\n}\n\n#hello-option {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    font-size: 6rem;\n    border-radius: 75px;\n    animation: wave 2.1s 0.6s infinite;\n}\n\n#hobbies-options {\n    display: flex;\n    flex-direction: column;\n    gap: 8px;\n\n    > div:first-child {\n        font-size: x-large;\n        text-align: center;\n    }\n}\n\n#hello-option, #animation-btn, #chess-btn {\n    aspect-ratio: 1 / 1;\n    width: 150px;\n}\n\n#animation-btn, #chess-btn {\n    position: relative;\n    border-radius: 10px;\n    overflow: hidden;\n}\n\n#about-options video {\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    object-fit: cover; \n    transform: translate(-50%, -50%);\n}\n\n#animation-btn video {\n    width: 80%;\n    height: 80%;\n}\n\n#chess-btn video {\n    width: 58%;\n    height: 80%;\n}\n\n.light-theme .selected {\n    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;\n}\n\n.dark-theme .selected {\n    box-shadow: 5px 3px 120px 30px rgba(255, 255, 255, 0.3),\n    2px 1px 10px 10px rgba(0, 0, 0, 0.4);\n}\n\n@keyframes wave {\n    0% {\n      transform: rotate(0deg);\n    }\n    10% {\n      transform: rotate(16deg);\n    }\n    20% {\n      transform: rotate(-6deg);\n    }\n    30% {\n      transform: rotate(16deg);\n    }\n    40% {\n      transform: rotate(-4deg);\n    }\n    50% {\n      transform: rotate(16deg);\n    }\n    60% {\n      transform: rotate(0deg);\n    }\n    100% {\n      transform: rotate(0deg);\n    }\n  }\n\n@media (max-width: 1172px) {\n    .slider-container {\n        flex-direction: column;\n    }\n\n    .inactive {\n        h2 {\n            transform: rotate(0deg);\n        }\n    }\n}\n\n@media (max-width: 800px) {\n    #about-box {\n        flex-direction: column;\n        gap: 16px;\n    }\n\n    #about-content {\n        order: 2;\n        margin: 0;\n    }\n\n    #about-options, #hobbies-options {\n        flex-direction: row;\n        justify-content: center;\n        align-items: end;\n    }\n\n    #hobbies-options {\n        display: grid;\n        grid-template-rows: min-content 150px;\n        grid-template-columns: 150px 150px;\n        gap: 8px;\n\n        > div:first-child {\n            grid-column: 1 / 3;\n        }\n    }\n}\n\n@media (max-width: 692px) {\n    #about-options, #hobbies-options {\n        gap: 8px;\n    }\n\n    #hello-option {\n        min-width: 150px;\n        min-height: 150px;\n        max-width: 150px;\n        max-height: 150px;\n    }\n\n    #hobbies-options {\n        display: flex;\n        flex-direction: column;\n        gap: 8px;\n        height: 150px;\n\n        > div:first-child {\n            display: none;\n        }\n    }\n\n    #animation-btn, #chess-btn {\n        background: none !important;\n        border: none !important;\n        width: 60px;\n        height: 60px;\n        box-shadow: none;\n    }\n\n    .animation-img-container {\n        flex-direction: column;\n    }\n}\n\n@media (max-width: 472px) {\n    .chess-stats-container {\n        flex-direction: column;\n\n        > div > div {\n            grid-column: 1 / -1;\n        }\n    }\n}"],"sourceRoot":""}]);
